import { useContext } from "react";
import { Link } from "react-router-dom";
import reducerContext from "../../../context/reducerContext";

export default function Announcement(props) {

  const { state } = useContext(reducerContext);

  return (
    <div className="col-12 col-sm-6 col-lg-3 my-1">
      <div className="card" style={{ height: '100%' }}>
        <div className="card-body">
          <div className="position-relative" style={{ width: '100%', height: '150px' }} >
            <div className="position-absolute " style={{ background: `url(${props.photo})`, backgroundPosition: 'center', backgroundSize: 'cover', top: 0, left: 0, right: 0, bottom: 0 }}></div>
          </div>
        </div>
        <div className="card-body d-flex flex-column justify-content-between" style={{ flexGrow: 1 }}>
          <div style={{ minHeight: '65px' }}>
            <p className="card-text mb-0">{props.title}</p>
          </div>
          <div>
            <p className="text-muted text-start mb-0">{props.city}, {props.date}</p>
            <div className="d-flex justify-content-between align-items-center mt-2">
              <span className="badge bg-secondary fs-6">{parseInt(props.price).toLocaleString()} zł</span>
              <Link to={`/announcement/${props.id}`} className={`btn btn-${state.theme} ms-auto`} >Pokaż</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}